  }
}
.chat-container {
  width: 66%;
  height: 99.6vh;
  /* border: 1px solid black; */
  margin: 0px auto;
  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: hidden;
}
.chat-upper {
  width: 100%;
  height: 10%;
  /* border: 2px solid black; */
  font-family: "Lato", sans-serif;
  font-size: 22px;
  padding: 13px 23px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.chat-home {
  height: 100%;
  width: 100%;
  border-radius: 6px;
  border: none;
  outline: none;
  font-size: 15px;
  padding: 12px 23px;
  background-color: #0095f6;
  font-weight: 700;
  color: rgb(231, 231, 231);
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat-home-link {
  text-decoration: none;
}
.chat-home:hover {
  background-color: #0094f6af;
  color: black;
}
.mainchat-container {
  width: 100%;
  height: 90%;
  border: 1px solid rgba(128, 128, 128, 0.603);
  display: flex;
  flex-direction: row;
}
.search-newchat {
  width: 100%;
  height: 9%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid black;
  box-sizing: border-box;
}
.chat-search {
  border: none;
  background-color: rgba(143, 143, 143, 0.253);
  padding: 8px 16px;
  margin-left: 7px;
  font-size: 15px;
  border-radius: 7px;
}
.newchat-icon {
  margin-right: 7px;
}
.newchat-icon:hover {
  color: #0094f6;
}
.contacts {
  height: 100%;
  width: 35%;
  border-right: 1px solid black;
}
.chat-coantainer-1 {
  width: 100%;
  height: 91%;
  /* border: 2px solid red; */
