    overflow: scroll;
    height: unset;
  }
}

.main__chatcontent {
    flex-grow: 1;
    padding: 20px 25px;
    border-right: 1px solid #ebe7fb;
  }
  .content__header {
    padding-bottom: 15px;
    border-bottom: 1px solid #ebe7fb;
  }
  .current-chatting-user {
    display: flex;
    align-items: center;
  }
  .current-chatting-user p {
    margin: 0;
    font-weight: 600;
  }
  .content__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .settings .btn-nobg {
    color: #000;
  }
  .content__body {
    max-height: calc(100vh - calc(70vh / 2));
    overflow: auto;
  }

  .content__body::-webkit-scrollbar {
    width: 10px;
  }
  .content__body::-webkit-scrollbar-track {
    background: #f1f1f1ab;
  }
  .content__body::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.808);
    border-radius: 12px;
    opacity: 0.6;
  }
  .content__body::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .chat__item {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 15px;
    transition: all 0.3s ease;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-animation-name: showIn;
            animation-name: showIn;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s; /* or: Xms */
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-direction: normal;
            animation-direction: normal; /* or: normal */
    -webkit-animation-timing-function: cubic-bezier(
      0.88,
      0.19,
      0.37,
      1.11
    );
            animation-timing-function: cubic-bezier(
      0.88,
      0.19,
      0.37,
      1.11
    ); /* or: ease, ease-in, ease-in-out, linear, cubic-bezier(x1, y1, x2, y2) */
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both; /* or: backwards, both, none */
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s; /* or: Xms */
  }
  @-webkit-keyframes showIn {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes showIn {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  .chat__items::-webkit-scrollbar {
    width: 8px;
  }
  
  /* Track */
  .chat__items::-webkit-scrollbar-track {
    background: #f1f1f1ab;
  }
  
  .chat__items::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.808);
    border-radius: 12px;
    opacity: 0.6;
  }
  
  /* Handle on hover */
  .chat__items::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .chat__item .avatar {
    margin-right: 0px;
    margin-left: 20px;
    background: transparent;
    padding: 1px;
  }
  .chat__item__content {
    background-color: #4462ff;
    color: #fff;
    padding: 15px;
    border-radius: 10px 10px 0 10px;
    max-width: 50%;
    min-width: 215px;
  }
  .chat__item__content .chat__meta {
    justify-content: space-between;
    display: flex;
    margin-top: 10px;
  }
  .chat__item__content .chat__meta span {
    font-size: 14px;
    color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .chat__msg {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .chat__item.other {
    flex-direction: row-reverse;
    -webkit-transform-origin: left;
            transform-origin: left;
  }
  .chat__item.other .chat__item__content {
    background-color: #fff;
    color: #000;
    border-radius: 10px 10px 10px 0;
    max-width: 50%;
  }
  .chat__item.other .avatar {
    margin-right: 20px;
    margin-left: 0px;
  }
  .chat__item.other .chat__item__content .chat__meta span {
    color: #d1d1d1;
  }
  .content__footer {
    padding-top: 22px !important;
  }
  .sendNewMessage {
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-radius: 8px;
  }
  .sendNewMessage button {
    width: 36px;
    height: 36px;
    background-color: #ecefff;
    border: none;
    box-shadow: none;
    outline: none;
    cursor: pointer;
    font-size: 16px;
    color: #4665ff;
    padding: 0;
    border-radius: 5px;
    line-height: 36px;
    transition: all 0.3s cubic-bezier(0.88, 0.19, 0.37, 1.11);
  }
  .sendNewMessage button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .sendNewMessage button i {
    display: block;
  }
  .sendNewMessage input {
    flex-grow: 1;
    padding: 0 15px;
    background-color: transparent;
    border: none;
    outline: none;
  }
  #sendMsgBtn {
    background-color: #3b5bfe;
    color: #fff;
  }
  
  @media (max-width: 720px)  {
    .main__chatcontent {
      max-width: 100%;
    }
  }

  @media (max-width: 600px)  {
    .content__body {
      max-height: calc(100vh - calc(75vh / 2));
    }
    .main__chatcontent {
      padding: 20px 5px;
    }
  }
