    border: none;
    border-bottom: 1px solid black;
    border-radius: 0px;
    padding: 5% 2%;
    box-sizing: border-box;
  }
  .sidemenu-btn:hover {
    border: none;
    border-bottom: 1px solid black;
  }
}

.community-question-container {
  height: 100vh;
  width: 70vw;
  margin: 0px auto;
  padding: 12px;
