.postdetail-container {
  display: flex;
  display: flex;
  flex-direction: column;
  /* height: 100vh; */
  width: auto;
  /* border: 2px solid red; */
}

.recommend-text-postdetail {
  text-align: center;
  /* font-size: 18px; */
  /* font-weight: 700; */
  border: outset;
  border-radius: 15px;
  margin: 20px 20px;
}

.postdetail-container-upper {
  width: 100%;
  /* height: 44%; */
  box-sizing: border-box;
  display: flex;
  /* flex-direction: row; */
  align-items: center;
  flex-direction: column;
  background: linear-gradient(180deg,   #ececec, transparent);
  padding: 27px;
}
.profile-user-postdetail {
  width: 23%;
}
.postdetail-main-compo {
  display: flex;
  height: 100vh;
}
.postdetail-main-compo-meme {
  display: flex;
  height: auto;
}
.postdetail-container-upper-slider {
  height: 100%;
  /* border: 2px solid black; */
  width: 27%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.postdetail-container-upper-slider-meme {
  height: 100%;
  /* border: 2px solid black; */
  width: 82%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider {
  width: 320px;
  height: auto;
}

.slider-meme {
  width: 100%;
  height: auto;
}
.slider-div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.slider-image {
  max-width: 100%;
  /* vertical-align: middle; */
  margin-top:2px;
  max-height: 350px;
  object-fit: contain;
}
.postdetail-container-upper-info {
  /* border: 2px solid red; */
  width: 71%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* padding-left: 45px; */
  box-sizing: border-box;
}
.showquink-detail {
  margin-left: 10px;
  /* font-size: 23px; */
  font-weight: bold;
  margin-top: 6.5px;
  margin-bottom: 0;
}

.showavatar-detail {
  display: none;
}

.postdetail-container-upper-info-1 {
  font-size: 30px;
  padding: 11px;
  text-align: center;
  font-weight: 700;
}
.postdetail-container-upper-info-4 {
  font-size: 26px;
  padding: 11px;
  font-weight: 700;
  padding-top: 35px;
    padding-bottom: 15px;
}
.suggestion-postdetail {
  /* width: 20%; */
  margin: 0px 10px;
  overflow: scroll;
  overflow-x: hidden;
}
.postdetail-container-upper-info-2 .MuiIconButton-root {
  /* background-color: white; */
  margin: 6px 12px;
}
.swipeButtons__share {
  color: black !important;
}
.swipeButtons__like {
  color: #ec5e6f !important;
}
.swipeButtons__comment {
  color: #62b4f9 !important;
}
.no-span {
  margin: 0px 5px;
}
.postdetail-container-body {
  /* margin-left: 3px; */
  border-left: thick solid #fe8787;
  /* border: groove; */
  /* width: 60%; */

  /* margin-right: 3px; */
  /* box-shadow: 3px 2px 5px 3px rgb(94 94 94); */
  box-shadow: 0 0 2px 2px rgb(159 158 158 / 51%);
  border-top: 1px solid #999;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 6px 80px 10px 80px;
  font-size: 19px;
  height: 56%;
  /* overflow-y: scroll; */
  /* overflow-x: hidden; */
  /* word-wrap:break-word; */
}

