}

.postdetail-profile {
  margin: 23px 25px;
  padding: 12px;
  height: 123px;
  border-radius: 12px;
  box-shadow: 5px 5px 15px rgb(0 0 0 / 40%);
  /* background-color: #2b4c7425; */
  background-color: #0094f61f;
  color: black;
}


.suggestion-postdetail::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* Track */
.suggestion-postdetail::-webkit-scrollbar-track {
  margin-top: 7px;
  background: #f1f1f1ab;
}

/* Handle */
.suggestion-postdetail::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.808);
  border-radius: 12px;
  opacity: 0.6;
}

/* Handle on hover */
.suggestion-postdetail::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.pdetail-card-respo::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

/* Track */
