@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap");
.loginbox {
  /* border: 0.2px solid black; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 24px 126px;
  /* background-color: rgb(255, 255, 255); */
  background-color: white;
  /* opacity: 0.; */
  border-radius: 14px;
  box-shadow: 1px 1px 12px 0.2px rgb(90, 90, 90);
  max-width: 100%;
  max-width: 100%;
  z-index: 2;
}
.position-1 {
  position: absolute;
  top: 90px;
  left: 44px;
}
.position-2 {
  position: absolute;
  top: 10px;
  left: 44px;
}
.loginbox h1 {
  font-size: 36px;
  font-family: "Oswald", sans-serif;
  font-weight: 100;
}
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
.button-login {
  width: 100%;
  padding: 16px 126px;
  cursor: pointer;
  margin: 12px;
  border: none;
  border-radius: 24px;
  outline: none;
  background-color: #0095f6;
  color: white;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 800;
}
.button-toggle {
  width: 100%;
  cursor: pointer;
  padding: 11px 12px;
  margin: 12px;
  border: none;
  border-radius: 24px;
  outline: none;
  border: 3px solid #1b2838;
  color: #1b2838;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 800;
  background-color: white;
}
.info-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  /* border: 2px solid red; */
  width: 100%;
}
.info-box label {
  margin: 6px 1px;
  font-family: "Montserrat", sans-serif;
}
.info-box input {
  font-size: 23px;
  margin: 7px 0px;
  border: none;
  border-bottom: 1px solid black;
  /* background-color: white; */
  outline: none;
  width: 98%;
}
.info-box input:hover {
  border-bottom: 1px solid #0095f6;
}
@media screen and (max-width: 1300px) {
  .loginbox {
    /* border: 2px solid red; */
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 7px 16px;
    background-color: rgb(255, 255, 255);
    opacity: 0.7;
    border-radius: 0px;
    box-shadow: 1px 1px 12px 0.2px rgb(90, 90, 90);
    z-index: 2;
    box-sizing: border-box;
    position: absolute;
    left: 0px;
  }
  .watermark {
    display: none;
  }
  .info {
    display: none;
  }
  .services-quink {
    margin: 700px 15px 0px 15px;
  }
}
